.footer {
    position:relative;
    display: flex;
    align-items: center;
    text-align: center;
    bottom:0;
    left:0;
    width:100%;
    height: 90px;
  
}

/*............. fix this.................*/
.footer-links ul {
  position:absolute;
  top:0%;
  left:50%;
  transform: translate(-50%, -100%);
  margin:0;
  padding:0;
  display:flex;
  }
  .footer-text{
    position:absolute;
    top:80%;
    left:50%;
    transform: translate(-50%, -100%);
    margin:0;
    padding:0;
    display:flex;
  }
  
  ul li {
    list-style: none;
  }
  
  ul li a {
    position: relative;
    width:60px;
    height:60px;
    display:block;
    text-align:center;
    margin:0 10px;
    border-radius: 50%;
    padding: 6px;
    box-sizing: border-box;
    text-decoration:none;
    box-shadow: 0 10px 15px rgba(0,0,0,0.3);
    background: linear-gradient(0deg, #ddd, #fff);
    transition: .5s;
  }
  
  ul li a:hover {
    box-shadow: 0 2px 5px rgba(0,0,0,0.3);
    text-decoration:none;
  }
  
  ul li a .fab {
    widtH: 100%;
    height:100%;
    display:block;
    background: linear-gradient(0deg, #fff, #ddd);
    border-radius: 50%;
    line-height: calc(70px - 12px);
    font-size:26px;
    color: #262626;
    transition: .5s;
  }
  
  ul li:nth-child(1) a:hover .fab {
    color: #3b5998;
  }
  
  ul li:nth-child(2) a:hover .fab {
    color: #00aced;
  }
  
  ul li:nth-child(3) a:hover .fab {
    color: #000;

  }
  
  ul li:nth-child(4) a:hover .fab {
    color: #007bb6;
  }
  
  ul li:nth-child(5) a:hover .fab {
    color: #000;
  }

  @media (max-width: 450px) {
    .footer-text{
     position:absolute;
     top:80%;
     left:50%;
     transform: translate(-50%, -100%);
     margin:0;
     padding:0;
     display:flex;
    }
  }